// filter-range
var $range = $(".filter-range__slider");

$range.ionRangeSlider({
	type: "double",
	min: 5,
	max: 500,
	keyboard: true,
});

$range.on("change", function () {
	var $this = $(this),
	from = $this.data("from"),
	to = $this.data("to"),
	val1 = $(this).parent().find('.filter-range__input--from'),
	val2 = $(this).parent().find('.filter-range__input--to');

	val1.val(from);
	val2.val(to);
});

$('.filter-range__input').on('change', function(){
	var val1 = $('.filter-range__input--from').val(),
	val2 = $('.filter-range__input--to').val()

	var slider = $(".filter-range__slider").data("ionRangeSlider");
	slider.update({
		from: val1,
		to: val2,
	});
})