// card-image
$('.card-image__link').on('click', function(){
	var src = $(this).attr('data-image');
	$(this).parent().parent().siblings('.card-image__lg').css({'background-image':'url('+src+')'});
	$(this).parent().parent().siblings('.card-image__lg').css({'background-repeat':'no-repeat'});
	$(this).parent().parent().siblings('.card-image__lg').css({'background-position':'center center'});

	$('.card-image__sm').removeClass('card-image__sm--active');
	$(this).parent().addClass('card-image__sm--active');
})

$(window).load(function(){
	var src = $('.card-image__link:first').attr('data-image');
	$('.card-image__lg').css({'background-image':'url('+src+')'});
	$('.card-image__lg').css({'background-repeat':'no-repeat'});
	$('.card-image__lg').css({'background-position':'center center'});
})