function elem(){
	if($(window).width() + 17 < 1232){
		if(!$('.wrap-tovar').find('.card-info').length){
			$('.wrap-tovar').prepend($('.card-info'));
		}

		if(!$('.card-info__desc').next('.card-image').length){
			$('.card-info__desc').after($('.card-image'));
		}

		if(!$('.card-image').next('.card-table').length){
			$('.card-image').after($('.card-table'));
		}

		if(!$('.card-table').next('.card-block--bg').length){
			$('.card-table').after($('.card-block--bg'));
		}

		if(!$('.card-block--bg').next('.card-actions').length){
			$('.card-block--bg').after($('.card-actions'));
		}

		if(!$('.card-actions').next('.card-info__price').length){
			$('.card-actions').after($('.card-info__price'));
		}
	}else if($(window).width() + 17 > 1232){
		if($('.wrap-tovar').find('.card-info').length){
			$('.wrap-tovar2').prepend($('.card-info'));
		}

		if($('.card-info__desc').next('.card-image').length){
			$('.wrap-tovar').prepend($('.card-image'));
		}

		if($('.card-image').next('.card-table').length){
			$('.card-actions').after($('.card-table'));
		}

		if($('.card-table').next('.card-block--bg').length){
			$('.card-table').after($('.card-block--bg'));
		}

		if($('.card-block--bg').next('.card-actions').length){
			$('.card-info__price').after($('.card-actions'));
		}

		if($('.card-actions').next('.card-info__price').length){
			$('.card-info__desc').after($('.card-info__price'));
		}
	}
}
elem()

$(window).resize(function(){
	elem()
})