// tovars-slider
$('.tovars-slider').slick({
	slidesToShow: 5,
	slidesToScroll: 1,
	responsive: [
	{
		breakpoint: 1199,
		settings: {
			slidesToShow: 3,
			slidesToScroll: 1,
		}
	},
	{
		breakpoint: 767,
		settings: {
			slidesToShow: 2,
			slidesToScroll: 1,
		}
	},
	{
		breakpoint: 512,
		settings: {
			slidesToShow: 1,
			slidesToScroll: 1,
		}
	}
	]
})